import { Storage } from '@/utils';

const defaultAccessUser = {
    name: 'Lunacian',
}

export const auth = {

    namespaced: true,

    state:
    {
        name: 'Lunacian',
        games: [],
        isActive: false,

        userLoading: false,
        registerLoading: false,
        saveLoading: false,
        updateLoading: false,
        removeLoading: false,
    },

    mutations:
    {
        setUser: (state, data) =>
        {
            state.name = data.name;
            state.games = data.games;
            state.isActive = true;
        },

        updateName: (state, data) =>
        {
            state.name = data;
        },

        appendGame: (state, data) =>
        {
            state.games.unshift(data);
        },

        removeGame: (state, data) =>
        {
            state.games.splice(state.games.indexOf(data), 1);
        },

        setLoading: (state, { action, boolean }) =>
        {
            state[`${action}Loading`] = boolean;
        },

        resetState: (state) =>
        {
            state.name = 'Lunacian';
            state.games = [];
            state.isActive = false;
        },
    },

    actions:
    {
        register: ({ commit }, name) => 
		{
			commit('setLoading', { action: 'user', boolean: true });

            Storage.set(process.env.APP_LOCAL_STORAGE_NAME, name);

			commit('setUser', {
                name: name,
                games: []
            });
			
			commit('setLoading', { action: 'user', boolean: false });
		},

        saveName: ({ commit, getters }, name) => 
		{
			commit('setLoading', { action: 'save', boolean: true });

            commit('updateName', name);

            Storage.destroy(process.env.APP_LOCAL_STORAGE_NAME);

            Storage.set(process.env.APP_LOCAL_STORAGE_NAME, getters.name);
			
			commit('setLoading', { action: 'save', boolean: false });
		},

        saveGame: ({ commit, getters }, game) => 
		{
			commit('setLoading', { action: 'save', boolean: true });

            commit('appendGame', game);

            Storage.destroy(process.env.APP_LOCAL_STORAGE_GAMES);

            Storage.set(process.env.APP_LOCAL_STORAGE_GAMES, JSON.stringify(getters.games));
			
			commit('setLoading', { action: 'save', boolean: false });
		},

        removeGame: ({ commit, getters }, game) => 
		{
			commit('setLoading', { action: 'remove', boolean: true });

            commit('removeGame', game);

            Storage.destroy(process.env.APP_LOCAL_STORAGE_GAMES);

            Storage.set(process.env.APP_LOCAL_STORAGE_GAMES, JSON.stringify(getters.games));
			
			commit('setLoading', { action: 'remove', boolean: false });
		},

        loadUser: ({commit}, { name, games }) => 
		{
			commit('setLoading', { action: 'user', boolean: true });

			commit('setUser', { name, games });
			
			commit('setLoading', { action: 'user', boolean: false });
		},

        forceSignout: async ({ commit }) =>
        {
            await Storage.destroy(process.env.APP_LOCAL_STORAGE_NAME);
            await Storage.destroy(process.env.APP_LOCAL_STORAGE_GAMES);

            // commit('resetState');
        },

        unset: ({ commit }) =>
        {
            commit('resetState');
        },
    },

    getters:
    {
        name: state => state.name,

        games: state => state.games,

        isActive: state => state.isActive,

        isLoading: state => action => state[`${action}Loading`],
    },
};



