<template>
    <div class="vh-text-center">
        <p class="vh-margin-remove">
            WELCOME TO
        </p>
        <h1 class="axs-title vh-margin-remove">
            AXSynergy
        </h1>

        <div class="vh-margin-large-top">
            <form class="vh-form-stacked"
                @submit.prevent="submit()">
                <fieldset class="vh-fieldset">
                    <div class="vh-margin">
                        <label class="vh-form-label vh-text-bold">
                            What is your gaming name?
                        </label>
                        <input
                            type="text"
                            class="axs-input"
                            required="true"
                            autofocus="true"
                            placeholder="ex. LUNACIAN"
                                v-model="form.name">

                        <small class="vh-text-danger vh-text-bold"
                            v-if="hasError('name')">
                            👎🏻 Please fill up this required field.
                        </small>
                    </div>
                </fieldset>

                <div class="vh-margin-top">
                    <vh-button
                        type="submit"
                        :size="'large'"
                        :color="'default'">
                        🚀 LET'S GO
                    </vh-button>
                </div>
            </form>
        </div>

        
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

import { Form, Toast } from '@/utils';

export default
{
    name: 'Register',

    data()
    {
        return {

            errors: {},

            form:
            {
                name: 'LUNACIAN'
            },
        }
    },

    methods:
    {
        ...mapActions({
            register: 'auth/register',
        }),

        submit()
        {
            this.errors = {};

            if (!this.form.name)
            {
                this.errors.name= ['Please fill this required field.'];

                Toast.error({
                    message: 'Oops, kindly double check your form.',
                });
            }

            this.register(this.form.name);

            this.$router.push('/dashboard');
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },
    }
};
</script>



