import Vue from 'vue';
import Router from 'vue-router';
import { Storage } from '@/utils';
import routes from './routes';

Vue.use(Router);

export const router = new Router({
    mode: 'hash',
    transitionOnLoad: true,
    // linkActiveClass: 'active',
    linkExactActiveClass: 'active',
    root: '/',
    routes,
});

router.beforeEach((to, from, next) =>
{
    const notAuthRequired = ['register'];
	const authRequired = !notAuthRequired.includes(to.meta.page);

    const hasName = Storage.get(process.env.APP_LOCAL_STORAGE_NAME) !== null;

    // if (hasAccessToken && to.path === '/') return next('/dashboard');

    if(authRequired && !hasName) return next('/register');

	if(!authRequired && hasName) return next('/');

	if(to.path == '/') return next('/dashboard');

    return next();
});



