<template>
    <vh-modal
        :isOpen.sync="$parent.$data.accountModal"
        :isCenter="true">

        <vh-modal-body>
            <div class="vh-margin-top vh-text-center">
                <h1 class="vh-text-title">
                    🤖
                </h1>

                <div class="vh-margin vh-form-stacked">
                    <label class="vh-form-label vh-text-bold">
                        Gaming Name
                    </label>
                    <input
                        type="text"
                        class="axs-input"
                        required="true"
                        placeholder="ex. LUNACIAN"
                            v-model="form.name"
                            @input="update()">
                </div>

                <div class="vh-margin-medium-top">
                    <vh-button
                        type="button"
                        :size="'large'"
                        @click="leave()"
                        :color="'default'">
                        LOGOUT
                    </vh-button>
                </div>
            </div>
        </vh-modal-body>
    </vh-modal>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

import { Form, Toast, Dater, Storage } from '@/utils';

export default
{
    data()
    {
        return {
            errors: {},

            form:
            {
                name: '',
            },
        };
    },

    created()
    {
        this.initialize();
    },

    computed:
    {
        ...mapGetters({
            name: 'auth/name',
        }),

        accountModal()
        {
            return this.$parent.$data.accountModal;
        },
    },

    methods:
    {
        ...mapActions({
            save: 'auth/saveName',
            leave: 'auth/forceSignout',
        }),
        
        async leave()
        {
            this.errors = {};

            await Storage.destroy(process.env.APP_LOCAL_STORAGE_NAME);

            await Storage.destroy(process.env.APP_LOCAL_STORAGE_GAMES);
            
            window.location.href = process.env.APP_URL;
        },

        async update()
        {
            this.save(this.form.name);
        },

        initialize()
        {
            this.form.name = this.name;
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },

        closeModal()
        {
            this.$parent.toggleModal('account', false);
        },
    },

    watch:
    {
        accountModal()
        {
            this.initialize();
        },
    },
};
</script>



