export default {
    functional: true,

    props:
    {
        label:
        {
            type: String,
            default: null,
        },
    },

    render(h, { data, props })
    {
        return h(
            'div',
            {
                staticClass: data.staticClass,

                class:
                [
                    'vh-card',
                    'vh-card-body',
                    'vh-card-small',
                    'vh-padding-small',
                    'vh-text-center',
                ],
            },
            [
                h(
                    'h3',
                    {
                        class: 'vh-margin-remove'
                    },
                    
                    props.label
                ),

                h(
                    'img',

                    {
                        attrs:
                        {
                            class:
                            [
                                'vh-margin-small-top',
                            ],
                            src: '/static/assets/images/empty.png',
                            alt: 'Empty Illustration',
                            width: '400',
                            height: '400',
                        },
                    },
                ),
            ]
        );
    },
};



