import Vue from 'vue';
import Toasted from 'vue-toasted';

Vue.use(Toasted);

export const Toast = {
    success: (data) =>
    {
        let form = {
            message: data.message,
            type: 'success',
        };

        Toast.fire(form);
    },

    error: (data) =>
    {
        let form = {
            message: data.message,
            type: 'error',
        };

        Toast.fire(form);
    },

    fire: (data) =>
    {
        Vue.toasted.show(
            data.message,
            {
                ...data,
                position: 'bottom-right',
                duration: 5000,
                closeOnSwipe: true,
            }
        );
    },
};



