import { Text } from '@/utils';

export default {
    functional: true,

    props:
    {
        divider:
        {
            type: Boolean,
            default: false,
        },

        gutter:
        {
            type: String,
            default: null, // 'small', 'medium', 'large', 'collapse'
        },

        matchHeight:
        {
            type: Boolean,
            default: false,
        },
    },

    render(h, { props, data, children })
    {
        return h(
            'div',

            {
                staticClass: data.staticClass,

                class: Text.joinClass(
                    'vh-grid',
                    props.gutter && `vh-grid-${props.gutter}`,
                    props.divider && 'vh-grid-divider',
                    props.matchHeight && 'vh-grid-match'
                ),
            },

            children
        );
    },
};



