<template>
    <div id="app">

        <div class="vh-margin-auto vh-width-xxlarge vh-margin-medium-top">

            <vh-grid
                :gutter="'small'"
                class="vh-visible@m">
                <div class="vh-width-expand">
                    <h1 class="axs-name"
                        v-if="$route.meta.page != 'register'">
                        AXSynergy
                    </h1>
                </div>
                <div class="vh-text-right"
                    v-if="isActive">
                    <a class="vh-link-reset"
                        @click="toggleModal('account', true)">
                        <h2 class="axs-name">
                            🤖 {{name}}
                        </h2>
                    </a>
                    
                </div>
            </vh-grid>

            <div class="vh-hidden@m vh-text-center">
                <h1 class="axs-name"
                    v-if="$route.meta.page != 'register'">
                    AXSynergy
                </h1>

                <a class="vh-link-reset"
                    v-if="isActive"
                    @click="toggleModal('account', true)">
                    <h2 class="axs-name">
                        🤖 {{name}}
                    </h2>
                </a>
            </div>

            <div class="vh-card vh-card-body vh-card-white vh-box-shadow-medium vh-card-content">
                <div v-if="isActive">

                    <router-view/>

                </div>
                
                <div v-else>

                    <register-component />

                </div>

            </div>
        </div>

        <div class="vh-margin-small-top vh-text-center">
            <small class="axs-footer">
                YZTS &middot; BETA v1
            </small>
        </div>

        <account-modal />

    </div>
</template>

<script>
import { mapGetters } from 'vuex';

import RegisterComponent from '@/views/auth/register';
import AccountModal from '@/views/components/modals/account';

export default
{
    name: 'App',

    components:
    {
        RegisterComponent,
        AccountModal,
    },

    data()
    {
        return {
            accountModal: false,
        };
    },

    computed:
    {
        ...mapGetters({
            name: 'auth/name',
            isActive: 'auth/isActive'
        }),

    },

    methods:
    {
        toggleModal(name, bool)
        {
            this[`${name}Modal`] = bool;
        },
    },
};
</script>


<style lang="scss">
    @import "./scss/app";
</style>



