export default {
    functional: true,

    props:
    {
        type:
        {
            type: String,
            default: 'bar' // bar, image
        },

        size:
        {
            type: String,
            default: 'small',
        },

        width:
        {
            type: String,
            default: '100%'
        },

        height:
        {
            type: String,
        },
    },

    render(h, {data, props})
    {
        return h(
            'div',
            {
                staticClass: data.staticClass,

                class:
                [
                    'vh-placeholder',
                    `vh-placeholder-${props.type}`,
                    `vh-placeholder-${props.type}-${props.size}`,
                    'vh-animate',
                    'vh-animate-infinite',
                    'vh-animate-shimmer',
                ],
                
                style:
                {
                    width: props.width,
                    height: props.height,
                },
            }
        );
    },
};



