// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import { store } from './stores';
import { router } from './router';
import { Storage } from './utils';

// Global Components
import './components';

if (Storage.get(process.env.APP_LOCAL_STORAGE_NAME) !== null)
{
    store.dispatch(
        'auth/loadUser',
        {
            name: Storage.get(process.env.APP_LOCAL_STORAGE_NAME),
            games: Storage.get(process.env.APP_LOCAL_STORAGE_GAMES)
                ? JSON.parse(Storage.get(process.env.APP_LOCAL_STORAGE_GAMES))
                : []
        }
    );
}
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App),
});



