import { Text } from '@/utils';

export default {
    props:
    {
        click:
        {
            type: Function,
            default: null,
        },
    },

    render(h)
    {
        return h(
            'a',

            {
                class: Text.joinClass(
                    'vh-close',
                    'vh-modal-close',
                    'vh-link-reset'
                ),

                on:
                {
                    click: (e) =>
                    {
                        this.$emit('click', e);
                    },
                },
            },

            [
                h(
                    'i',
                    {
                        class: 'fa fa-times',
                    },
                    '❌'
                ),
            ]
        );
    },
};



